"use client";

import Link from "next/link";
import { ThemeToggle } from "@/components/ThemeToggle";
import { BatteryChip } from "@/components/BatteryChip";
import { useAppStore } from "@/lib/store";

export function NavBar() {
  const battery = useAppStore((state) => state.battery);

  return (
    <header className="sticky top-0 z-40 w-full border-b bg-bg/95 backdrop-blur supports-[backdrop-filter]:bg-bg/60">
      <div className="container flex h-14 items-center justify-between px-4">
        <Link href="/" className="flex items-center space-x-2">
          <span className="font-bold text-lg">EV Co-Pilot</span>
        </Link>
        <div className="flex items-center gap-2">
          <BatteryChip percentage={battery} />
          <ThemeToggle />
        </div>
      </div>
    </header>
  );
}
